.bcar {
  width: 500px;
}

.bcentr {
  display: flex;
  justify-content: center;
  margin-left: 47%
}

.bcar-asub {
    display: flex;
     justify-content: center;
     padding: 13px;
     color: #2dce89;
}
.bcar-dsub {
    display: flex;
     justify-content: center;
     padding: 13px;
     color: #f5365c;
}
.bcar-usub {
    display: flex;
     justify-content: center;
     padding: 13px;
     color: #fb6340;
}

.bcar-body {
  display: flex;
  justify-content: center;
  margin: 30px;
}

.drop_div {
  display: flex;
  justify-content: center;
  align-items: center;
}
.drop_min_height {
  min-height: 650px;
}
.drop_card {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 50px;
  padding: 50px;
  width: 50%;
  border-radius: 20px;
}
.drop {
  background-color: #eeeeee;
  outline-style: dashed;
  outline-color: #bdbdbd;
  outline-width: medium;
}
.drop_zone {
  padding: 25px;
  font-weight: bold;
  color: #9e9e9e;
  margin-bottom: 120px;
}

.backdrop {
  z-index:  1;
  color: '#fff';
}
